body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.CircleSelector_CircleSelector__50I-i {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 200px;
    width: 150px;
    border: 4px solid purple;
    border-radius: 8px;
    margin-right: 20px;
  }
  
  .CircleSelector_CircleSelector__50I-i button {
    height: 30px;
    width: 125px;
    background-color: purple;
    color: white;
    border-radius: 4px;
    text-transform: uppercase;
    cursor: pointer;
    outline: none;
  }
  
  .CircleSelector_CircleSelector__50I-i button:hover {
    background-color: limegreen;
  }
  
  /* Adding a class of 'selected' to the selected button dynamically is secret sauce */
  .CircleSelector_CircleSelector__50I-i button.CircleSelector_selected__1zSqp {
    background-color: white;
    color: limegreen;
    border: none;
    cursor: default;
  }
.Circles_Circles__2Yuzn {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-items: center;
    height: 200px;
    width: 200px;
    border: 4px solid limegreen;
    border-radius: 8px;
  }
  
  .Circles_Circles__2Yuzn div {
    height: 75px;
    width: 75px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: limegreen;
    color: white;
    font-size: 30px;
    border: 5px solid limegreen;
    border-radius: 50%;
    margin: auto 10px;
  }
  
  /* Another dose of dynamic className goodness is the winning ticket */
  .Circles_Circles__2Yuzn div.Circles_selected__1DT-X {
    background-color: white;
    color: purple;
    border: 5px solid purple;
  }
body, * {
  box-sizing: border-box;
}

.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.App-header {
  width: 100%;
  padding: 20px;
  font-size: 24px;
  background-color: grey;
  color: white;
  text-align: center;
  letter-spacing: 3px;
}

.App main {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex: 1 1;
}
